/* ======================== contact ======================== */



/* ========= aside ========= */
.contact__container {
    background: var(--color-bg1);
    padding: 4rem;
    display: grid;
    grid-template-columns: 40% 60%;
    gap: 4rem;
    height: 30rem;
    margin: 7rem auto;
    border-radius: 1rem;
}

.contact__aside {
    background: var(--color-primary);
    padding: 3rem;
    border-radius: 1rem;
    position: relative;
    bottom: 10rem;
}

.aside__image {
    width: 12rem;
    margin-bottom: 2rem;
}

.contact__aside h2 {
    text-align: right;
    margin-bottom: 1rem;
}

.contact__aside p {
    font-size: 0.85rem;
    margin-bottom: 2rem;
}

.contact__details li {
    display: flex;
    gap: 1rem;
    align-items: center;
    margin-bottom: 1rem;
}

.contact__socials {
    display: flex;
    gap: 0.8rem;
    margin-top: 3rem;
}

.contact__socials a {
    font-size: 0.9rem;
}
.badge:hover{
    background: transparent;
    cursor: pointer;
}



    /* ========= form ========= */
.contact__form {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    margin-right: 4rem;
}

.form__name {
    display: flex;
    gap: 1.2rem;
}

.contact__form input[type="text"] {
    width: 50%;
}

.contact__form input, textarea {
    width: 100%;
    padding: 1rem;
    background: var(--color-bg);
    color: var(--color-white);
    transition: var(--transition);
}

.contact__form input:focus {
    background: var(--color-black);
} 

.contact__form textarea:focus {
    background: var(--color-black);
}

.contact__form .btn {
    width: max-content;
    cursor: pointer;
    margin-top: 1rem;
    font-weight: bold;
}





/* ======================== SCREEN : tablets ======================== */
@media screen and (max-width: 1024px) {
    .contact {
        padding-bottom: 0;
    }
    .contact__container {
        gap: 1.5rem;
        margin-top: 3rem;
        height: auto;
        padding: 1.5rem;
    }
    .contact__aside {
        margin-left: 1.5rem;
        width: auto;
        padding: 1.5rem;
        bottom: 0;
    }
    .contact__form {
        align-self: center;
        margin-right: 1.5rem;
    }
}





/* ======================== SCREEN : mobile ======================== */
@media screen and (max-width: 600px) {
    .contact__container {
        grid-template-columns: 1fr;
        gap: 3rem;
        margin-top: 0;
    }
    .contact__form {
        margin: 0 1.5rem 3rem;
    }
    .form__name {
        flex-direction: column;
    }
    .form__name input[type="text"] {
        width: 100%;
    }
}
